/*-------------------------------
    Popup Styles
  ---------------------------------*/

.block-bg {
  background: rgba(22, 22, 27, 0.97) none repeat scroll 0 0;
  height: 100%;
  left: 0;
  opacity: 0;
  position: fixed;
  text-align: center;
  top: 0;
  -webkit-transform: scale(0.7);
  transform: scale(0.7);
  transition: -webkit-transform 0.8s ease 0s, transform 0.8s ease 0s,
    opacity 0.5s ease 0s, visibility 0.5s ease 0s;
  visibility: hidden;
  width: 100%;
  z-index: 999;
}

.block-bg.is-visible {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
  visibility: visible;
}

.block-bg .close__wrap {
  color: white;
  cursor: pointer;
  font-size: 12px;
  font-weight: 700;
  line-height: 25px;
  margin-top: -12.5px;
  padding-right: 25px;
  position: absolute;
  right: 15px;
  text-transform: uppercase;
  top: 50%;
  z-index: 11;
  transition: 0.4s;
}

.block-bg .close__wrap:hover {
  color: #e59285;
}

.block-bg .close__wrap::after {
  content: "\f136";
  font-family: Material-Design-Iconic-Font;
  font-size: 24px;
  position: absolute;
  right: 0;
  top: 0;
}

